import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import { MuiPickersUtilsProvider , KeyboardTimePicker, KeyboardDatePicker } from '@material-ui/pickers';
import MomentUtils from '@date-io/moment';
import moment from 'moment';

import { BasicDetailsStyle } from './components/testCreationElements';

const BasicDetails = ({values, handleChange, nextStep, testDate, handleTestDate, startTime, handleStartTime, endTime, handleEndTime }) => {
    const dateFormatter = str => {
        return str;
    };
    return(
        <>
            <BasicDetailsStyle>
              <center><h5>Basic Information</h5></center>
            <AppBar title='Enter Basic Details' />
            <TextField
              placeholder='Enter Test Name'
              label='Name'
              margin='normal'
              fullWidth
              variant='outlined'
              backgroundColor='secondary'
              defaultValue={values.name}
              onChange={handleChange('name')}
            />
            <br />
            <TextField
              placeholder='Enter Test Description'
              label='Description'
              margin='normal'
              fullWidth
              rows={4}
              multiline
              variant='outlined'
              backgroundColor='secondary'
              defaultValue={values.description}
              onChange={handleChange('description')}
            />
            <br />
            <Grid container={true}>
            <Grid item>
            <TextField
              placeholder='Enter Test Duration in minutes'
              label='Duration (minutes)'
              margin='normal'
              variant='outlined'
              backgroundColor='secondary'
              type='number'
              defaultValue={values.duration}
              onChange={handleChange('duration')}
              sx={{ my: 10 }}
            />
            </Grid>
            <Grid item alignItems='stretch' style={{ display: 'flex' }}>
            <Box ml = {5}>
            <TextField
              placeholder='Enter Number of Attempts'
              label='No. of Attempts'
              margin='normal'
              variant='outlined'
              backgroundColor='secondary'
              type='number'
              defaultValue={values.noOfAttempts}
              onChange={handleChange('noOfAttempts')}
            />
            </Box>
            </Grid>
            </Grid>
            <br />
            {/* <label htmlFor='testDate'>Test Date</label>
            <Calendar id='testDate' value={testDate} onChange={handleTestDate} dateFormat='dd/mm/yy' showIcon />
            <br />

            <label htmlFor='startTime'>Start Time</label>
            <Calendar id='startTime' value={startTime} onChange={handleStartTime} timeOnly showIcon />

            <label htmlFor='endTime'>End Time</label>
            <Calendar id='endTime' value={endTime} onChange={handleEndTime} timeOnly showIcon />
            <br /> */}

            <MuiPickersUtilsProvider libInstance={moment} utils={MomentUtils}>
            <KeyboardDatePicker
                variant='inline'
                margin='normal'
                label='Start Date'
                value={testDate}
                onChange={handleTestDate}
                format='yyyy/MM/DD'
                rifmFormatter={dateFormatter}
            />              
            <KeyboardTimePicker
                variant='inline'
                margin='normal'
                label='Start Time'
                value={startTime}
                onChange={handleStartTime}
                rifmFormatter={dateFormatter}
            />
            <KeyboardTimePicker
                variant='inline'
                margin='normal'
                label='End Time'
                value={endTime}
                onChange={handleEndTime}
                rifmFormatter={dateFormatter}
            />
            </MuiPickersUtilsProvider>


            <Button
              color='primary'
              variant='contained'
              onClick={nextStep}
            >Continue</Button>
            </BasicDetailsStyle>
        </>
    )
}
export default BasicDetails;