import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router';
import { FormControlLabel , Checkbox } from '@material-ui/core';
import { DataTable } from 'primereact/datatable';
import { Column } from 'primereact/column';
import SelectTags from '../question/selectTags.temp';
import { BasicDetailsStyle } from './components/testCreationElements';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import { Paginator } from 'primereact/paginator';
import { Icon } from '@iconify/react';
import QuestionDataService from '../../core/actions/question';
import {
  Button,
  ButtonSml
} from './components/testCreationElements'


function getSessionStorageOrDefault(key, defaultValue) {
  const stored = sessionStorage.getItem(key);
  if (!stored) {
    return defaultValue;
  }
  return JSON.parse(stored);
}

function setSessionStrage(key, values) {
  sessionStorage.setItem(key, JSON.stringify(values));
}

const QuestionDetails = ({ nextStep, prevStep , tags , handleTagChanges, isManual, handleCheckboxChange , selectedQuestions, handleSelection }) => {
  const history = useHistory();
  const [questions, setQuestions] = useState([]);
  const [currentPage, setCurrentPage] = useState(0);
  const [rows, setRows] = useState(5);
  const [totalRecords, setTotalRecords] = useState(0);
  const schema = yup.object().shape({
    text: yup.string(),
    tag: yup.string(),
    optionText: yup.string(),
    optionTag: yup.string()
  });
//   onRowSelect(event) {
//     this.toast.show({ severity: 'info', summary: 'Product Selected', detail: `Name: ${event.data.name}`, life: 3000 });
// }
  const { register, handleSubmit, reset, getValues } = useForm({
    resolver: yupResolver(schema),
    defaultValues: getDefaultValues()
  });

  function getDefaultValues() {
    const { text, tag, optionText, optionTag } = getSessionStorageOrDefault(
      'TestQuestion',
      {
        text: '',
        tag: '',
        optionText: '',
        optionTag: ''
      }
    );
    return {
      text: text,
      tag: tag,
      optionText: optionText,
      optionTag: optionTag
    };
  }

  const search = async (
    text,
    tag,
    optionText,
    currentPage,
    rows
  ) => {
    QuestionDataService.getAllQuestions(
      text,
      tag,
      optionText,      
      currentPage,
      rows
    )
      .then((response) => {
        const { totalItems, currentPage, totalPages, questions } =
          response.data;
          console.log('search function==>', response.data);
        setQuestions(questions);
        console.log('questions==>', questions);
        setCurrentPage(currentPage);
        console.log('currentPage==>', currentPage);
        setTotalRecords(totalItems);
        console.log('totalItems==>', totalItems);
        // setSessionStrage('QuestionList', {
        //   text: text,
        //   tag: tag,
        //   optionText: optionText,
        //   currentPage: currentPage,
        //   rows: rows
        // });
      })
      .catch((e) => {
        console.log('error from server:', e.message);
      });
  };

  useEffect(() => {
    // const { text, tag, optionText, optionTag, currentPage, rows } =
    //   getSessionStorageOrDefault('TestQuestion', {
    //     text: '',
    //     tag: '',
    //     optionText: '',
    //     optionTag: '',
    //     currentPage: 0,
    //     rows: 10
    //   });
    const text = '';
    const optionText = '';
    search(text, tags, optionText, currentPage, rows);
  }, []);

  const submitForm = (data) => {
    console.log(data);
    const { text, tag, optionText, optionTag } = data;
    search(text, tag, optionText, optionTag, currentPage, rows);
  };

  const onPageChange = async (e) => {
    const { page, rows } = e;
    await setCurrentPage(page);
    await setRows(rows);
    const { text, tag, optionText, optionTag } = getValues();
    search(text, tag, optionText, optionTag, currentPage, rows);
  };

  const handleChanges = (event) => {
    handleTagChanges(event);
    console.log('tags==>', tags);
    const text = '';
    const optionText = '';
    search(text, tags, optionText, currentPage, rows);
  }

  const tagsBodyTemplate = (rowData) => {
    let tagStr = '';
    rowData.tags.forEach(tag => {
      tagStr = tagStr + tag +', ';
    });
    return <span>{tagStr}</span>;
};

const editQuestion = async (question) => {
  var id = question.id;
  var path = `/update_question/${id}`;
  console.log('path==>', path);
  history.push(path);
};

const viewQuestion = async (question) => {
  var id = question.id;
  var path = `/view_question/${id}`;
  history.push(path);
};

  const actionBtnTemplate = (rowData) => {
    return (
      <>
        <ButtonSml width='40px' onClick={() => viewQuestion(rowData)}>
          <Icon
            icon='fluent:reading-mode-mobile-24-regular'
            color='${(props) => props.theme.primaryColor}'
            width='20'
            height='20'
          />
        </ButtonSml>
      </>
    );
  };

  return (
    <>
      <BasicDetailsStyle>
        <div>
          <h1>Question Details</h1>
          <br />
          <div>
            <SelectTags id='tags' value={tags} name='tags' handleTags={handleChanges} showAdd={false} />
            <br />
          </div>
          <br />
          <FormControlLabel control={<Checkbox checked={isManual} onChange={handleCheckboxChange}></Checkbox>} label='Is Manual' />
          <br />
          { isManual ? 
            (<>
                <div className='card'>
                <DataTable 
                  selectionMode='checkbox'
                  selection={selectedQuestions}
                  onSelectionChange={handleSelection} dataKey='id'
                  value={questions}
                  scrollable
                  scrollHeight='500px'
                  style={{ width: '100%' }}
                >
                    <Column
                      selectionMode='multiple'
                      sortable={true}
                      field='selectQuestion'
                      header='Select'
                      headerStyle={{ width: '10%' }}
                      columnKey='selectedQuestion'
                    ></Column>
                    <Column
                      sortable={true}                
                      field='text'
                      header='Text'
                      headerStyle={{ width: '50%' }}
                      columnKey='text'
                    />
                    <Column
                      field='tags'
                      header='Tags'
                      headerStyle={{ width: '30%' }}
                      body={tagsBodyTemplate}
                    />
                    <Column
                      body={actionBtnTemplate}
                      style={{ width: '10%' }}
                    />
                  </DataTable>
                </div>
              </>)
            :
            (<>
            </>)
          }
          <br />
        </div>
        <Button color='secondary' variant='contained' onClick={prevStep}>
          Back
        </Button>

        <Button color='primary' variant='contained' onClick={nextStep}>
          Continue
        </Button>
      </BasicDetailsStyle>
    </>
  );
};
export default QuestionDetails;
