import React ,{ useState } from 'react';
import BasicDetails from './basicDetails';
import QuestionDetails from './questionDetails';
import Confirm from './confirm';
import moment from 'moment';
import { useRouteMatch } from 'react-router-dom';
import { useHistory } from 'react-router';

import {
    FormContainer
} from './components/testCreationElements';

import {
    CompanyTitleContainer,
    CompanyTitle,
    CompanyLogo
} from '../question/components/QuestionElements';

const TestTemplate = () => {
    const [step, setStep] = useState(1);
    // const [questions, setQuestions] = useState([]);
    const [values, setValues] = useState({
        name: '',
        description: '',
        duration: '',
        noOfAttempts: ''
    });

    const history = useHistory();
    const match = useRouteMatch();
    const [tags, setTags] = useState([]);
    const [testDate, handleTestDateChange] = useState(moment());
    const [startTime, handleStartTimeChange] = useState(moment());
    const [endTime, handleEndTimeChange] = useState(moment());
    const [isManual, handleCheckbox] = useState(false);
    const [questions, setSelectedQuestion] = useState([]);
    console.log(startTime,endTime);
    const handleSelection = (e) => {
        setSelectedQuestion(e.value);
    }
    const handleTagChanges = (event) => {
        if(event.target.name === 'tags')
            setTags(event.target.value);
    }
    const handleTestDate = (date) => {
        handleTestDateChange(date);
    };
    const handleStartTime = (date) => {
        handleStartTimeChange(date);
    };
    const handleEndTime = (date) => {
        handleEndTimeChange(date);
    };
    const handleChange = (prop) => (event) => {
        setValues({ ...values, [prop]: event.target.value });
    };
    const handleCheckboxChange = (e) => {
        handleCheckbox(e.target.checked);
    };
    console.log(isManual);
    const finalStep = () => {
        console.log(values.name,values.description,values.duration,isManual);
        if(isManual){
            setTags([]); 
            console.log(questions);
        }
        else{
            setSelectedQuestion([]);
            console.log(tags);
        }
    }
    const nextStep = () => {
        setStep(step + 1);
        console.log('step==>', step);
    };
    const prevStep = () => {
        setStep(step - 1);
    };

    const exitTestCreation = () => {
        history.goBack();
    }


    return(
        <>
        <CompanyTitleContainer>
          <CompanyLogo>
            <CompanyTitle>IMATMI</CompanyTitle>
          </CompanyLogo>
        </CompanyTitleContainer>
        <FormContainer>
        {(() => {
            switch (step) {
                case 1:
                    return (
                    <BasicDetails
                        values={values}
                        handleChange={handleChange}
                        nextStep={nextStep}
                        testDate={testDate}
                        handleTestDate={handleTestDate}
                        startTime={startTime}
                        handleStartTime={handleStartTime}
                        endTime={endTime}
                        handleEndTime={handleEndTime}
                    />
                    );
                case 2:
                    return (
                    <QuestionDetails
                        nextStep={nextStep}
                        prevStep={prevStep}
                        tags={tags}
                        handleTagChanges={handleTagChanges}
                        isManual={isManual}
                        handleCheckboxChange={handleCheckboxChange}
                        selectedQuestions={questions} 
                        handleSelection={handleSelection}
                    />
                    );
                case 3:
                    return (
                    <Confirm
                        nextStep={finalStep}
                        prevStep={prevStep}
                        values={values}
                        testDate={testDate}
                        startTime={startTime}
                        endTime={endTime}
                        isManual={isManual}
                        tags={tags}
                        questions={questions}
                    />
                    );
                case 4:
                    return(
                        <>
                        <center>
                            Test Created Successfully
                        </center>
                        {/* <Button onClick={() => exitTestCreation()}>Exit</Button> */}
                        </>
                    );
                }
            })()}
            </FormContainer>
        </>
    )
}

export default TestTemplate;