import 'bootstrap/dist/css/bootstrap.min.css';
import React, { useEffect, useRef } from 'react';
import { Provider } from 'react-redux';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import { ThemeProvider } from "styled-components";
import store from '../store';
import './app.scss';
import { loadUser } from './core/actions/authentication';
import AuthGuard from './core/guards/authentication.guard';
import { setAuthToken } from './core/services/central-operations.service';
import Layout from './layout';
import Dashboard from './modules/dashboard/dashboard';
import AddEditQuestion from './modules/question/add.edit.question';
import QuestionList from './modules/question/question.list';
import { Pages } from './shared/constants/routes';
import DisplayTests from './modules/test-all/displayTests';
import ViewQuestion from './modules/question/viewQuestion'
import TestComponent from './modules/testPlatform/test.component';
import TestTemplate from './modules/testTemplate/testTemplate';
import TestResult from './modules/testPlatform/testResult'
// import QuestionTemp from './modules/question/oldKeep/question.temp';

require('./core/interceptors');

if (localStorage.token) {
  setAuthToken(localStorage.token);
}

// const theme = {
//   primaryColor : '#19B3Bd',
//   secondaryColor: '#E72C59',
//   dangerColor: '#800000',
//   textColor: '#333E48',
//   formBackgroundColor: '#ABD7E4',
//   skyBlue: '#4990EF',
//   lightPink:'#F84973',
//   white: '#FFFFFF'
// };

// primaryColor : '#4285F4',
const theme = {
  primaryColor : '#143560',
  secondaryColor: '#F0EBF8',
  dangerColor: '#D93025',
  textColor: '#5F6368',
  formBackgroundColor: '#FFFFFF',
  skyBlue: '#4990EF',
  lightPink:'#F84973',
  white: '#FFFFFF',
  borderColor: '#DADCE0',
  inputAreaHoverColor: '#F1F3F4'
};

const App = (props) => {
  const toast = useRef(null);
  useEffect(() => {
    store.dispatch(loadUser());
  }, []); //read react.js documentation for explanation

  return (
    <ThemeProvider theme={theme}>
      <Provider store={store}>
      <Layout>
        <section className='container-fluid p-0'>
          <div className='wrapper'>
            {/* <Toast ref={toast} />
            <Button
              label='Success'
              className='p-button-success'
              onClick={showMessage(
                toast,
                AlertSeverity.Error,
                'Sumaary',
                'details'
              )}
            /> */}
            <Switch>
              <AuthGuard
                exact
                path={Pages.dashboard.link}
                component={Dashboard}
              />
              <AuthGuard
                exact
                path={Pages.add_question.link}
                component={AddEditQuestion}
              />
              <AuthGuard
                exact
                path={Pages.update_question.link}
                component={AddEditQuestion}
              />
              <AuthGuard
                exact
                path={Pages.list_questions.link}
                component={QuestionList}
              />
              <AuthGuard
                exact
                path={Pages.view_question.link}
                component={ViewQuestion}
              />              
              {/* <AuthGuard
                exact
                path='/temp'
                component={QuestionTemp}
              /> */}
              <AuthGuard
                exact
                path={Pages.test_creation.link}
                component={TestTemplate}
              />
              <AuthGuard
                exact
                path={Pages.test_platform.link}
                component={TestComponent}
              />  
              <AuthGuard
                exact
                path={Pages.all_tests.link}
                component={DisplayTests}
              /> 
              <AuthGuard
                exact
                path={Pages.test_result.link}
                component={TestResult}
              />           
            </Switch>
          </div>
        </section>
        <script src='/socket.io/socket.io.js'></script>
      </Layout>
    </Provider>
    </ThemeProvider>
  );
};

export default App;
