import React, {useState, useEffect} from 'react'
import Box from '@material-ui/core/Box';
import Tab from '@material-ui/core/Tab';
import PropTypes from 'prop-types';
import Tabs from '@material-ui/core/Tabs';
import Typography from '@material-ui/core/Typography';
import TestTemplateDataService from '../../core/actions/testTemplate';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import { useHistory } from 'react-router-dom';
import { Modal } from 'react-bootstrap';
import DisplayUsers from './displayUsers';

 function TabPanel(props) {
    const { children, value, index, ...other } = props;
  
    return (
      <div
        role='tabpanel'
        hidden={value !== index}
        id={`full-width-tabpanel-${index}`}
        aria-labelledby={`full-width-tab-${index}`}
        {...other}
      >
        {value === index && (
          <Box sx={{ p: 3 }}>
            <Typography>{children}</Typography>
          </Box>
        )}
      </div>
    );
  }
  function a11yProps(index) {
    return {
      id: `full-width-tab-${index}`,
      'aria-controls': `full-width-tabpanel-${index}`
    };
  }
  
const DisplayTests = () => {
    const [value, setValue] = useState(0);
    const [testId, setTestId] = useState('');
    let history = useHistory();
    const [selectedUsers, setUsers] = useState([]);
    const handleSelection = (e) => {
      setUsers(e.value);
    }
    const handleChange = (event, newValue) => {
      setValue(newValue);
    };
    const [open, setOpen] = React.useState(false);

    const handleOpen = (id) => {
      setTestId(id);
      setOpen(true);
    };
    const handleClose = () => {
      setOpen(false);
    };

  const handleSave = async () => {
    await TestTemplateDataService.assignManyTestTemplate(testId, selectedUsers);
    handleClose();
  }

  const [tests, setTests] = useState([]);

  useEffect( async () => {
    const data = await TestTemplateDataService.getAllTests();
    setTests(data.data);
    console.log(data.data);
  }, [])

  const handleStart = (e) => {
    const path = '/test/' + e;
    history.push(path);
  }
  var cardStyle = {
    display: 'block',
    width: '250px',
    transitionDuration: '0.3s',
    height: '300px',
    backgroundColor: '#e3e3e3'
  }
  return (
    <>
    <Box sx={{ width: '100%'}}>
      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={value} onChange={handleChange} aria-label='basic tabs example' centered>
          <Tab label='All' {...a11yProps(0)} />
          <Tab label='Completed' {...a11yProps(1)} />
          <Tab label='In Progess' {...a11yProps(2)} />
        </Tabs>
      </Box>
      <TabPanel value={value} index={0}>
        <div style={{'display':'flex', 'flexWrap' : 'wrap', 'flexDirection':'row', 'rowGap':'20px', 'padding' : '20px', 'columnGap' : '40px', 'marginRight': 'auto', 'marginLeft': 'auto', 'marginTop' : '50px'}}>
        {tests.length && tests.map((test, index) =>{
          return (
          <div key={index}>
              <Card sx={ { minWidth: 300 } } style={cardStyle}>
                <CardContent>
                  <Typography sx={{ fontSize: 14 }} color='text.secondary' gutterBottom>
                    {test.testName}
                  </Typography>
                  <Typography variant='h5' component='div'>
                    {test.testName}
                  </Typography>
                  <Typography sx={{ mb: 1.5 }} color='text.secondary'>
                    {test.testDescription} <br/>
                  </Typography>
                  <Typography variant='body2'>
                    Test Date : {new Date(test.testDate).getDate()} / {new Date(test.testDate).getMonth()+1} / {new Date(test.testDate).getFullYear()}
                    <br/>
                    Start Time : {new Date(test.startTime).getUTCHours()} : {new Date(test.startTime).getUTCMinutes()}
                    <br />
                    End Time : {new Date(test.endTime).getUTCHours()} : {new Date(test.endTime).getUTCMinutes()}
                    <br/>
                    Duration : {test.testDuration} minutes
                    <br/>
                    No. of questions : {test.questions.length}
                  </Typography>
                </CardContent>
                <CardActions>
                  <Button size='small' color='primary'
                    variant='contained'
                  onClick={(e) => (handleStart(test.id))}

                >Start Test</Button>
                <Button size='small' color='primary'
                    variant='contained'
                    onClick={(e) => (handleOpen(test.id))}
                >Assign Test</Button>
                </CardActions>
              </Card>
          </div>
          )}
        )}
        </div>
      </TabPanel>
      <TabPanel value={value} index={1}>
        Completed Tests here
      </TabPanel>
      <TabPanel value={value} index={2}>
        In progess tests here
      </TabPanel>
    </Box>
    
    <Modal
      show={open}
      onHide={() => setOpen(false)}
      size='lg'
      dialogClassName='modal-90w'
      >
      <Modal.Header closeButton>
          <Modal.Title id='contained-modal-title-vcenter'>
              Assign Tests to Users
          </Modal.Title>
      </Modal.Header>
      <Modal.Body>
          <DisplayUsers selectedUsers={selectedUsers} handleSelection={handleSelection}/>
      </Modal.Body>
      <Modal.Footer>
      <Button variant='secondary' onClick={handleClose}>
          Close
      </Button>
      <Button variant='primary' onClick={handleSave}>
          Save 
      </Button>
      </Modal.Footer>
      </Modal>
            </>
  );
}

export default DisplayTests;
