import React, { useState, useEffect, useRef } from 'react';
import { useHistory } from 'react-router';

import { useForm, Controller } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';

import { DataTable } from 'primereact/datatable';
import { Column } from 'primereact/column';
import { Paginator } from 'primereact/paginator';
import { Icon } from '@iconify/react';
import { SelectButton } from 'primereact/selectbutton';
import { MultiSelect } from 'primereact/multiselect';

import QuestionDataService from '../../core/actions/question';
import TagDataService from '../../core/actions/tag';

import {
  CompanyTitleContainer,
  CompanyTitle,
  CompanyLogo,
  QuestionListForm,
  Column1,
  Heading,
  Input,
  SearchItem,
  QuestionArea,
  QuestionTextArea,
  QuestionButtonContainer,
  Label,
  LabelBlack,
  Row,
  Button,
  ButtonSml,
  ButtonIcon,
  DangerButtonSml,
  RowHeading,
  ListItem,
  TagList,
  ButtonPrimary,
  ButtonDanger,
  AddQuestion
} from './components/QuestionElements';

function getSessionStorageOrDefault(key, defaultValue) {
  const stored = sessionStorage.getItem(key);
  if (!stored) {
    return defaultValue;
  }
  return JSON.parse(stored);
}

function setSessionStrage(key, values) {
  sessionStorage.setItem(key, JSON.stringify(values));
}

const QuestionList = () => {
  const history = useHistory();
  const [sort, setSort] = useState([{ field: 'text', order: 1 }]);
  const [questions, setQuestions] = useState([]);
  const [currentPage, setCurrentPage] = useState(0);
  const [rows, setRows] = useState(10);
  const [totalRecords, setTotalRecords] = useState(0);
  const [isTable, setIsTable] = useState(true);
  const [value1, setValue1] = useState('Table');
  const [selectedTags, setSelectedTags] = useState(null);
  const [tagList, setTagList] = useState([]);
  const dt = useRef(null);
  
  const onTagsChange = (e) => {
    dt.current.filter(e.value, 'tags', 'contains');
    setSelectedTags(e.value);
}

  const tagsFilter = <MultiSelect 
                        filter
                        filterBy='label' 
                        value={selectedTags} 
                        options={tagList} 
                        onChange={onTagsChange} 
                        placeholder='All' 
                        className='p-column-filter'
                        display='chip' />;

  const tableOptions = [
    { icon: 'pi pi-table', value: 'Table' },
    { icon: 'pi pi-list', value: 'List' }
  ];

  const tableTemplate = (option) => {
    return <i className={option.icon}>&nbsp;{option.value}</i> ;
  };

  const schema = yup.object().shape({
    text: yup.string(),
    tag: yup.array(),
    optionText: yup.string()
  });

  function getDefaultValues() {
    const { text, tag, optionText } = getSessionStorageOrDefault(
      'QuestionList',
      {
        text: '',
        tag: '',
        optionText: ''
      }
    );
    return {
      text: text,
      tag: tag,
      optionText: optionText
    };
  }

  const { register, handleSubmit, reset, getValues, control } = useForm({
    resolver: yupResolver(schema),
    defaultValues: getDefaultValues()
  });

  const search = async (
    text,
    tag,
    optionText,
    sort,
    currentPage,
    rows
  ) => {
    // console.log('tag==>', tag);
    // console.log('sort==>', sort);
    QuestionDataService.getAllQuestions(
      text,
      tag,
      optionText,
      sort,
      currentPage,
      rows
    )
      .then((response) => {
        const { totalItems, currentPage, totalPages, questions } = response.data
        setQuestions(questions);
        setCurrentPage(currentPage);
        setTotalRecords(totalItems);
        setSessionStrage('QuestionList', {
          text: text,
          tag: tag,
          optionText: optionText,
          sort: sort,
          currentPage: currentPage,
          rows: rows
        });
      })
      .catch((e) => {
        console.log('error from server:', e.message);
      });
  };

  const loadTagList = async () => {
    const response = await TagDataService.getAllTags();
    // console.log('response.data', response.data);
    if(response.data) {
      const list = response.data;
      const tagList = list.map((t) => {
        return { value:t.tag, label: t.tag}; } 
      );
      setTagList(tagList)
      // console.log('tagList', tagList);
    } 
  }

  useEffect(() => {
    loadTagList();
    const { text, tag, optionText, currentPage, rows } =
      getSessionStorageOrDefault('QuestionList', {
        text: '',
        tag: '',
        optionText: '',
        
        currentPage: 0,
        rows: 10
      });
    search(text, tag, optionText, sort, currentPage, rows);
  }, []);

  const submitForm = (data) => {
    console.log(data);
    const { text, tag, optionText } = data;
    search(text, tag, optionText, sort, currentPage, rows);
  };

  const onPageChange = async (e) => {
    const { page, rows } = e;
    await setCurrentPage(page);
    await setRows(rows);
    const { text, tag, optionText } = getValues();
    search(text, tag, optionText, sort, currentPage, rows);
  };

  const editQuestion = async (question) => {
    var id = question.id;
    var path = `/update_question/${id}`;
    console.log('path==>', path);
    history.push(path);
  };
  const viewQuestion = async (question) => {
    var id = question.id;
    var path = `/view_question/${id}`;
    history.push(path);
  };

  const addQuestion = async () => {
    var path = `/add_question`;
    console.log('path==>', path);
    history.push(path);
  };

  const deleteQuestion = async (question, index) => {
    console.log('Question To Delete==>', question);
    QuestionDataService.deleteQuestion(question.id, question);
  };

  const removeQuestion = async (question, index) => {
    console.log('Question To Remove==>', question);
    QuestionDataService.removeQuestion(question.id, question);
  };

  const viewQuestionTable = (rowData) => {
    console.log('rowData==>', rowData);
  };

  const onIndexTemplate = (rowData, props) => {
    return props.rowIndex + 1;
  };

  const onSort = async (e) => {
    console.log('onSort==>', e);
    await setSort(e.multiSortMeta)
    const { text, tag, optionText } = getValues();
    search(text, tag, optionText, e.multiSortMeta, currentPage, rows);
  }

  const onTableChange = (e) => {
    setValue1(e.value);
    if (e.value === 'Table') {
      setIsTable(true);
    } else {
      setIsTable(false);
    }
  };

  
  const tagsBodyTemplate = (rowData) => {
    let tagStr = '';
    rowData.tags.forEach(tag => {
      tagStr = tagStr + tag +', ';
    });
    return <span>{tagStr}</span>;
};

  const actionBodyTemplate = (rowData) => {
    return (
      <>
        <ButtonSml width='40px' onClick={() => viewQuestion(rowData)}>
          <Icon
            icon='fluent:reading-mode-mobile-24-regular'
            color='${(props) => props.theme.primaryColor}'
            width='20'
            height='20'
          />
        </ButtonSml>
        <ButtonSml width='40px' onClick={() => editQuestion(rowData)}>
          <Icon
            icon='clarity:note-edit-line'
            color='${(props) => props.theme.primaryColor}'
            width='20'
            height='20'
          />
        </ButtonSml>
        <DangerButtonSml width='40px' onClick={() => deleteQuestion(rowData)}>
          <Icon
            icon='ant-design:delete-outlined'
            color='${(props) => props.theme.dangerColor}'
            width='20'
            height='20'
          />
        </DangerButtonSml>
      </>
    );
  };

  return (
    <>
      <CompanyTitleContainer>
        <CompanyLogo>
          <CompanyTitle>IMATMI</CompanyTitle>
        </CompanyLogo>
      </CompanyTitleContainer>
      <QuestionListForm>
        <div>
          <Row>
            <Heading>Questions List</Heading>
          </Row>
          <Row>
            <SelectButton
              value={value1}
              options={tableOptions}
              itemTemplate={tableTemplate}
              onChange={onTableChange}
            />
            <AddQuestion>
              <ButtonPrimary onClick={addQuestion}>
                <ButtonIcon>
                  <Icon
                    icon='carbon:add-alt'
                    color='${(props) => props.theme.primaryColor}'
                    width='20'
                    height='20'
                  />
                  <span className=' d-none d-sm-none d-md-block '>Add New</span>
                </ButtonIcon>
              </ButtonPrimary>
            </AddQuestion>
          </Row>
          {isTable ? (
            <div className='card'>
              <DataTable
                ref={dt}
                value={questions}
                paginator
                rows={10}
                rowsPerPageOptions={[5, 10, 20, 50]}
                emptyMessage='No Questions found.'
                multiSortMeta={sort}
                onSort={onSort}
                sortMode='multiple'
                responsiveLayout='scroll'
              >
                <Column
                  field='Index'
                  header='Sr. No.'
                  body={onIndexTemplate}
                  style={{ width: '8%' }}
                />
                <Column
                  field='text'
                  filterField='text'
                  header='Question Text'
                  body={questions.text}
                  filter
                  filterPlaceholder='Question Text'
                  filterMatchMode='contains'
                  style={{ width: '50%' }}
                  sortable
                />
                <Column
                  field='tags'
                  filterField='tags'
                  header='Question Tags'
                  body={tagsBodyTemplate}
                  filter
                  filterElement={tagsFilter}
                  filterPlaceholder='Question Tags'
                  filterMatchMode='contains'
                  style={{ width: '27%' }}
                  sortable
                />
                <Column
                  body={actionBodyTemplate}
                  style={{ width: '15%' }}
                ></Column>
              </DataTable>
            </div>
          ) : (
            <>
            <div>
              {/* Search section */}
              <SearchItem>
                <form onSubmit={handleSubmit(submitForm)}>
                  <Row>
                    <Column1>
                      <LabelBlack>Question Text</LabelBlack>
                      <Input
                        type='text'
                        className='form-control'
                        {...register('text')}
                      />
                    </Column1>
                    <Column1>
                      <LabelBlack>Question Tag</LabelBlack>

                      <Controller
                        control={control}
                        name='tag'
                        render={({ field: { onChange, onBlur, value } }) => (
                        <MultiSelect 
                          filter filterBy='label' 
                          options={tagList}
                          onChange={(e) => { onChange(e.value); }}
                          onBlur={onBlur}
                          value={value}
                          placeholder='Select Tags' 
                          display='chip' />
                        )} 
                      />
                      {/* <Input
                        type='text'
                        className='form-control'
                        {...register('tag')}
                      /> */}
                    </Column1>
                    <Column1>
                      <LabelBlack>Option Text</LabelBlack>
                      <Input
                        type='text'
                        className='form-control'
                        {...register('optionText')}
                      />
                    </Column1>
                    <Column1>
                      <div>&nbsp; </div>
                      <Button type='submit'>
                        <ButtonIcon>
                          <Icon
                            icon='bx:bx-search-alt-2'
                            color='${(props) => props.theme.primaryColor}'
                            width='20'
                            height='20'
                          />
                          <span className=' d-none d-sm-none d-md-block '>
                            Search
                          </span>
                        </ButtonIcon>
                      </Button>
                    </Column1>
                  </Row>
                </form>
              </SearchItem>
              </div>
              <ul id='question-list' className='list-group'>
                {questions &&
                  questions.map((question, index) => (
                    <ListItem key={question.id}>
                      <RowHeading>
                        <Label>Question</Label>
                      </RowHeading>
                      <QuestionArea>
                        <QuestionTextArea
                          disabled
                          defaultValue={question.text}
                        />
                        <QuestionButtonContainer>
                          <ButtonPrimary
                            onClick={() => viewQuestion(question, index)}
                          >
                            <Icon
                              icon='fluent:reading-mode-mobile-24-regular'
                              color='${(props) => props.theme.primaryColor}'
                              width='20'
                              height='20'
                            />
                            &nbsp;View
                          </ButtonPrimary>
                          <ButtonPrimary
                            onClick={() => editQuestion(question, index)}
                          >
                            <Icon
                              icon='clarity:note-edit-line'
                              color='${(props) => props.theme.primaryColor}'
                              width='20'
                              height='20'
                            />
                            &nbsp;Edit
                          </ButtonPrimary>
                          <ButtonDanger
                            onClick={() => deleteQuestion(question, index)}
                          >
                            <Icon
                              icon='ant-design:delete-outlined'
                              color='${(props) => props.theme.primaryColor}'
                              width='20'
                              height='20'
                            />
                            &nbsp;Delete
                          </ButtonDanger>
                        </QuestionButtonContainer>
                      </QuestionArea>
                      <RowHeading>
                        <Label>Tags</Label>
                      </RowHeading>
                      <Row>
                        <TagList>
                          {question.tags &&
                            question.tags.map((tag, j) => (
                              <div>{tag},&nbsp;&nbsp;&nbsp;</div>
                            ))}
                        </TagList>
                      </Row>
                    </ListItem>
                  ))}
              </ul>
            {/* </div> */}
            <Paginator
              htmlFor='question-list'
              rows={rows}
              totalRecords={totalRecords}
              rowsPerPageOptions={[5, 10, 20, 50]}
              onPageChange={onPageChange}
            ></Paginator>
            </>
          )}
        </div>
        <Row>
          <Heading></Heading>
          <AddQuestion>
            <ButtonPrimary onClick={addQuestion}>
              <Icon
                icon='carbon:add-alt'
                color='${(props) => props.theme.primaryColor}'
                width='20'
                height='20'
              />
              &nbsp; Add New
            </ButtonPrimary>
          </AddQuestion>
        </Row>
      </QuestionListForm>

      <br></br>
      <br></br>
      <br></br>
      <QuestionListForm></QuestionListForm>
    </>
  );
};

export default QuestionList;
