import React from 'react';
import { BasicDetailsStyle } from './components/testCreationElements';
import Button from '@material-ui/core/Button';
import TestTemplateDataService from '../../core/actions/testTemplate';
import { convertDateTimeToServer} from '../date-utils';
import { useHistory } from 'react-router';

const Confirm = ({ nextStep, prevStep , values, testDate, startTime, endTime, isManual, tags, questions}) => {
    const history = useHistory();
    const onSubmit = () => {
        var newTestDate = convertDateTimeToServer(testDate);
        var newStartTime = convertDateTimeToServer(startTime);
        var newEndTime = convertDateTimeToServer(endTime);

        const newTestTemplate = {
            isVerified: 'true',
            verifiedBy: '',
            testName: values.name,
            testType: '',
            testDescription: values.description,
            testDuration: values.duration,
            testDate: newTestDate,
            startTime: newStartTime,
            endTime: newEndTime,
            isManual: isManual,
            noOfAttempts: values.noOfAttempts,
            tags: tags.map((tag)=> {
                return {
                    tag: tag,
                    weightage: 0
                }
            }),
            questions: questions.map((question)=> { return question.id })
        }

        TestTemplateDataService.createTestTemplate(newTestTemplate)
        .then((response) => {
            console.log('saveAndExit Ret Data==>', response.data);
            nextStep();
            const path = '/all-tests';
            history.push(path);
        })
        .catch((e) => {
            console.log('error==>', e);
        });
    }

    return(
        <BasicDetailsStyle>
            <center>
                <h3>Confirm Details</h3>
                <br />
                {/* {
                    questions && questions.map((question, index) => {
                        return (
                            <>                            
                            <h5>Question No. {index+1}</h5>
                            <SingleQuestion question={question} tickOptions={false}/>
                            <br></br>
                            <br></br>
                            </>
                        )
                    })
                } */}
                

                <Button color='secondary' variant='contained' onClick={prevStep}>
                Go Back
                </Button>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;                
                <Button color='primary' variant='contained' onClick={onSubmit}>
                    Save and Exit
                </Button>
                </center>  
        </BasicDetailsStyle>
    )
}
export default Confirm;